.haru-ripple {
  position: relative;
  overflow: hidden;
}
.haru-ripple .wave {
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.haru-ripple .animating.spread-enter-active {
  transition: transform 0.6s cubic-bezier(0, 0, 0.2, 1), opacity 0.6s cubic-bezier(0, 0, 0.2, 1);
}



